.carousel {
  position: relative;
  padding: 0 30px;
  margin-top: 50px;
  margin-bottom: 40px;

}
.carousel-container {
  position: relative;
  width: 100%;
  margin: auto;
  overflow: hidden;
}

.carousel-slide img {
  height: 246px;
  width: calc(100% / 3);
  margin: 0 4px;
}

.carousel-slide .video {
  width: 100%;
  margin: 0 4px;
}


.carousel-slide {
  display: flex;
  width: calc(100% - 24px);
  height: 246px;
}

#previousBtn, #nextBtn {
  position: absolute;
  top: 50%;
  z-index: 10;
  cursor: pointer;
  font-size: 30px;
  color: white;
  opacity: 0.5;
  transform: translateY(-50%);
}

#previousBtn {
  left: 0;
}

#nextBtn {
  right: 0;
}
