@import "src/scss/base/_mixins";

.hero {
  width: 100%;
  position: relative;
  img {
    width: 100%;
    height: auto;
    object-fit: contain;
  }
}

.call-upper {
  position: absolute;
  padding-right: inherit;
  padding-left: inherit;
  right: calc(50% / 10);
  bottom: 10px;
  width: 80px;
  height: 80px;
  justify-content: center;
  align-items: center;
  @include btn_anim();
  background-image: url("/img/ellipse_4.png");
  @include bg();
  svg {
    position: absolute;
    left: 50%;
    top: calc(50% - 3px);
    transform: translate(-50%, -50%);
  }
}
