@import "src/scss/global/variable";

.order {
    &_call-text {
        text-align: center;
        font-style: normal;
        font-size: 15px;
    }
    &_form {
        display: flex;
        flex-direction: column;
        margin-top: 30px;

        &-row {
            display: flex;
            flex-direction: column;
            margin-bottom: 20px;

            textarea {
                height: 134px;
            }
        }

        label {
            margin: 0 0 8px 0;
            padding: 0;
            font-style: normal;
            font-weight: 550;
            font-size: 16px;
        }
    }
}

.content-wrapper {
    display: flex;
    justify-content: space-between;
}

.content-box {
    width: calc(50% - 12px)
}

.politics {
     display: flex;
     align-items: center;
     margin: 20px auto 0;
     @media (max-width: 767px) {
         margin: 16px auto 0;
         align-items: flex-start;
     }
     label {
         margin: 0;
         padding-right: 6px;
         font-style: normal;
         font-weight: 500;
         font-size: 15px;
         color: $text;
         a {
             color: $text;
             transition: all ease-in-out .25s;
             &:hover {
                 color: $text;
                 text-decoration: none;
             }
         }
     }
     input[type=checkbox] {
         appearance: none;
         margin: 0 8px 0 0;
         display: inline-block;
         background: $black;
         border: 1px solid $text;
         width: 16px;
         max-width: 16px;
         height: 16px;
         flex-grow: 1;
         flex-shrink: 0;
         &:checked {
             background: $black url("data:image/svg+xml;base64,") no-repeat center center;
         }
     }
 }

.call-me {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-top: 35px;
    span {
        display: inline-block;
        margin-right: 18px;
        font-style: normal;
        font-weight: 500;
        font-size: 13px;
        color: rgba(255, 255, 255, .7);
    }
    a {
        display: inline-block;
        width: 100% / 25% - 12px;
        margin-right: 12px;
        text-decoration: none;
        font-style: normal;
        font-weight: 500;
        font-size: 13px;
        color: rgba(255, 255, 255, .7);
    }
}
