@import "../global/variable";
@import "../base/mixins";
.header {
  position: relative;
  display: flex;
  justify-content: flex-end;
  width: 100%;
  height: 156px;
  margin-bottom: 20px;
}
.logo {
  position: absolute;
  top: 15px;
  left: 50%;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  align-items: center;
  transform: translateX(-50%);
  span {
    margin: 8px 0;
    font-family: "Song", serif;
    font-style: normal;
    font-weight: normal;
    font-size: 25px;
    line-height: 40px;
  }
  img {
    max-width: 115px;
  }
}
.lang {
  display: flex;
  justify-content: flex-end;
  width: calc(100% - 30px);
  margin-top: 24px;
  a {
    font-family: 'Helvetica', sans-serif;
    font-style: normal;
    font-weight: 550;
    font-size: 22px;
    line-height: 28px;
    padding-left: 20px;
    color: $text;
    text-decoration: none;
  }

  .active {
    color: $link;
  }
}

.b-a {
  transition: all .25s;
  content: "";
  position: absolute;
  left: 0;
  height: 3px;
  width: 29px;
  background-color: rgba(255, 255, 255, 0.95);
}
.menu-collapsed {
  position: relative;
  margin-top: 24px;
  height: 30px;
  width: 30px;
  z-index: 2;
  cursor: pointer;
  ul {
    position: fixed;
    left: -9000px;
  }
}
.bar {
  @extend .b-a;
  position: absolute;
  left: 0;
  top: 12px;
  &:before {
    @extend .b-a;
    top: -8px;
  }
  &:after {
    @extend .b-a;
    top: 8px;
  }
}
.b-a-expanded {
  transition: all .25s;
  top: -0px;
}
.menu-expanded {
  position: fixed;
  margin-top: 0;
  text-align: center;
  height: 100vh;
  width: 100%;
  border-radius: 0;
  top: 0;
  left: 0;
  background-color: rgba(1, 2, 3, 0.75);
  ul {
    position: relative;
    left: 0;
    z-index: 2;
    li {
      line-height: calc(100vh / 4 - 10px);
    }
  }
  a {
    text-decoration: none;
    font-size: 2em;
    padding: 5px;
    color: $text;
    display:block;
    border: 1px solid transparent;
    &:hover {
      background-color:rgba($black, .45);
      color: $gold;
      border: 1px solid rgba(255, 255, 255, .15);
    }
  }
  .bar {
    left: 40px;
    top: 40px;
    background-color: transparent;
    transition: all .25s;
    z-index: 5;
    &:before {
      @extend .b-a-expanded;
      transform: rotate(45deg);
    }
    &:after {
      @extend .b-a-expanded;
      transform: rotate(-45deg);
    }

    &:hover {
      &:after, &:before {
        background: $red_gold;
      }
    }
  }
}
