@import "variable";
@import "src/scss/base/_mixins";

*,
*::before,
*::after {
	box-sizing: border-box;
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-center {
	justify-content: center;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.--dir-col {
	flex-direction: column;
}

html {
	font-size: 16px;
}

html,
body {
	min-height: 100%;
	position: relative;
}

body {
	font-family: "Arial", sans-serif;
	font-size: 1.375rem;
	background: $black;
	color: $text;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

section {
	padding: 0 calc(50vw - 590px);
}

section, footer, header {
	padding: 0 15px;
}

@media (min-width: 575.98px){
	section, footer, header {
		padding: 0 calc(50vw - 270px);
	}
}

@media (min-width: 767.98px){
	section, footer, header {
		padding: 0 calc(50vw - 360px);
	}
}

@media (min-width: 991.98px) {
	section, footer, header {
		padding: 0 calc(50vw - 480px);
	}
}

@media (min-width: 1199.98px){
	section, footer, header {
		padding: 0 calc(50vw - 590px);
	}
}

.custom-input {
	background-color: $black;
	border: 1px solid $gold;
	font-family: inherit;
	min-height: 44px;
	padding: 0 8px;
	color: $text;
	font-style: normal;
	font-weight: 500;
	font-size: 13px;
}
textarea.custom-input {
	padding: 10px 8px;
}

.custom-button {
	@include no-btn();
	@include btn_anim();
	background-color: $black;
	border: 1px solid $gold;
	height: 60px;
	color: $text;
	width: 70%;
	margin: 35px auto 0;
	text-transform: uppercase;
	font-weight: bold;
	font-size: 16px;
}

.line-horizontal {
	position: absolute;
	width: 100%;
	height: 2px;
	left: 0;
	background-repeat: repeat;
	background-image: url("data:image/svg+xml;base64,");
	&.top {
		top: 0;
	}
	&.bottom {
		bottom: 0;
	}
}


.line-vertical {
	position: absolute;
	top: 0;
	width: 2px;
	height: 100%;
	background-repeat: repeat;
	background-image: url("data:image/svg+xml;base64,");
	&.right {
		right: 0;
	}
	&.left {
		left: 0;
	}
}
