.tabs {
  position: relative;
  padding-top: 30px;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 10px;
  a {
    display: inline-block;
    text-align: center;
    width: 50%;
    order: 1;
  }
}

.heading {
  width: 100%;
}
.heading__text {
  fill: url(#img-pattern);
  &.active {
    fill: #5F61FB;
  }
}

.b-tab {
  padding: 20px;
  border: 1px solid #000;
  display: none;
  width: 100%;
  order: 3;
}

.b-tab.active {
  display: block;
}

.b-nav-tab {
  display: inline-block;
}

.b-nav-tab.active {
  color: #ff4200;
}
